/* eslint-disable */
<template>
  <div>
    <!-- <Checkbox @on-change="queryByTime()">按照时间进⾏查询及统计</Checkbox>
    <Checkbox @on-change="queryByMovieName()">按照电影进行查询及统计</Checkbox>
    <Checkbox @on-change="queryByDirectorName()">按照导演进行查询及统计</Checkbox>
    <Checkbox @on-change="queryByActorName()">按照演员进行查询及统计</Checkbox>
    <Checkbox @on-change="queryByMainActorName()">按照主演进行查询及统计</Checkbox>
    <Checkbox @on-change="queryByType()">按照电影类别进行查询及统计</Checkbox>
    <Checkbox @on-change="queryByCommenter()">按照评论者姓名进行查询及统计</Checkbox>
    <Checkbox @on-change="queryByScore()">按照评分进行查询及统计</Checkbox>
    <Checkbox @on-change="queryByCollaboration()">按照合作关系进行查询及统计</Checkbox>
    <Checkbox @on-change="queryBySimilarUser()">按照相似用户进行查询及统计</Checkbox>-->
    <RadioGroup v-model="query" @on-change="queryChange">
      <Radio label="queryByTime">按照时间进⾏查询及统计</Radio>
      <Radio label="queryByMovieName">按照电影名称进行查询及统计</Radio>
      <Radio label="queryByDirectorName">按照导演姓名进行查询及统计</Radio>
      <Radio label="queryByActorName">按照演员姓名进行查询及统计</Radio>
      <Radio label="queryByMainActorName">按照主演姓名进行查询及统计</Radio>
      <Radio label="queryByType">按照电影类别进行查询及统计</Radio>
      <!-- <Radio label="queryByVersion" >按照电影版本进行查询及统计</Radio> -->
      <Radio label="queryByCommenter">按照评论者姓名和评分进行查询及统计</Radio>
      <Radio label="queryByScore">按照评分范围进行查询及统计</Radio>
      <Radio label="queryCollaboration">按照合作关系进行查询及统计</Radio>
      <Radio label="querySimilarUser">按照相似用户进行查询及统计</Radio>
      <Radio label="queryBySql">按照sql语句进行查询及统计</Radio>
    </RadioGroup>
    <Form :model="formItem" :label-width="100">
      <FormItem label="Database">
        <RadioGroup v-model="database">
          <Radio label="MySql">MySql</Radio>
          <Radio label="Neo4j">Neo4j</Radio>
          <Radio label="HBase">HBase</Radio>
        </RadioGroup>
      </FormItem>

      <div v-show="queryByActorName">
        <Button @click="addActorName">添加一位演员</Button>
        <FormItem label="演员姓名">
          <Input
            v-for="(actorName, index) in formItem.actorName"
            v-model="formItem.actorName[index]"
            :key="index"
            placeholder="输入演员姓名..."
          ></Input>
        </FormItem>
      </div>
      <div v-show="queryByDirectorName">
        <Button @click="addDirectorName">添加一位导演</Button>
        <FormItem label="导演姓名">
          <Input
            v-for="(director, index) in formItem.directorName"
            v-model="formItem.directorName[index]"
            :key="index"
            placeholder="输入导演姓名..."
          ></Input>
        </FormItem>
      </div>
      <FormItem v-show="queryByMainActorName" label="主演姓名">
        <Input v-model="formItem.mainActorName" placeholder="输入主演姓名..."></Input>
      </FormItem>
      <FormItem v-show="queryByMovieName" label="电影名称">
        <Input v-model="formItem.movieName" placeholder="输入电影名称..."></Input>
      </FormItem>
      <FormItem v-show="queryByCommenter" label="评论者姓名">
        <Input v-model="formItem.commenterName" placeholder="输入评论者姓名..."></Input>
      </FormItem>
      <FormItem v-show="queryByCommenter||queryByScore" label="评分范围">
        <Slider v-model="formItem.scoreSlider" range :tip-format="tip" :max="50"></Slider>
      </FormItem>
      <FormItem v-show="queryByTime" label="时间范围">
        <Input v-model="formItem.time_from.day_of_month" placeholder="输入日期..."></Input>
        <Input v-model="formItem.time_from.day_of_week" placeholder="输入星期..."></Input>
        <Input v-model="formItem.time_from.month" placeholder="输入月份..."></Input>
        <Input v-model="formItem.time_from.quarter" placeholder="输入季度..."></Input>
        <Input v-model="formItem.time_from.year" placeholder="输入年份..."></Input>
      </FormItem>
      <FormItem v-show="queryCollaboration" label="合作关系">
        <Row>
          <Col span="11">
            合作类型：
            <Select v-model="formItem.collaborationType">
              <Option :value="1">合作的演员</Option>
              <Option :value="2">合作的演员和导演</Option>
              <Option :value="3">合作的导演</Option>
            </Select>
          </Col>
          <Col span="2" style="text-align: center">-</Col>
          <Col span="11">
            合作次数：
            <Input v-model="formItem.collaborationTime" placeholder="输入合作次数..."></Input>
          </Col>
        </Row>
      </FormItem>
      <div v-show="queryByType">
        <Button @click="addType">添加一种类型</Button>
        <FormItem label="类型">
          <Input
            v-for="(type, index) in formItem.type"
            v-model="formItem.type[index]"
            :key="index"
            placeholder="输入类型名称..."
          ></Input>
        </FormItem>
      </div>
      <div v-show="queryByVersion">
        <Button @click="addVersion">添加一种版本</Button>
        <FormItem label="版本">
          <Input
            v-for="(version, index) in formItem.version"
            v-model="formItem.version[index]"
            :key="index"
            placeholder="输入版本名称..."
          ></Input>
        </FormItem>
      </div>
      <FormItem v-show="queryBySql" label="SQL语句">
        <Input v-model="formItem.sql" placeholder="输入SQL..."></Input>
      </FormItem>
      <FormItem label="结果类型">
        <Select v-model="formItem.answer">
          <Option value="count">count</Option>
          <Option value="title">title</Option>
          <Option value="actor">actor</Option>
          <Option value="id">id</Option>
          <Option value="director">director</Option>
          <Option value="date">date</Option>
          <Option value="type">type</Option>
          <Option value="version">version</Option>
          <Option value="comment">comment</Option>
        </Select>
      </FormItem>
      <FormItem>
        <Button type="primary" @click="submit">Submit</Button>
        <Button style="margin-left: 8px">Cancel</Button>
      </FormItem>
    </Form>
    <div v-show="showResult" style="background:#eee;padding: 20px">
      <Row>
        <Col span="11">
          <Card>
            <p slot="title">查询时间(ms)</p>
            <p>{{result.time}}</p>
          </Card>
        </Col>
        <Col span="11" offset="2">
          <Card dis-hover>
            <p slot="title">查询结果数</p>
            <p>{{items.length}}</p>
          </Card>
        </Col>
      </Row>
    </div>
    <div class="overflow-auto" v-show="showResult">
      <b-table id="my-table" :items="items" :per-page="perPage" :current-page="currentPage" small></b-table>
      <b-pagination
        v-model="currentPage"
        :total-rows="rows"
        :per-page="perPage"
        aria-controls="my-table"
        align="center"
        :limit="10"
      ></b-pagination>
      <p class="mt-3">Current Page: {{ currentPage }}</p>
    </div>
  </div>
</template>
<script>
/* eslint-disable */
import axios from "axios";
export default {
  data() {
    return {
      query: "queryByTime",
      lastQuery: "queryByTime",
      formItem: {
        idList: [],
        actorNumber: 0,
        actorName: [new String()],
        answer: [],
        directorName: [new String()],
        mainActorName: null,
        commenterName: null,
        movieName: null,
        scoreSlider: [0, 0],
        type: [new String()],
        version: [new String()],
        time_from: {
          day_of_month: null,
          day_of_week: null,
          month: null,
          quarter: null,
          year: null
        },
        collaborationType: null,
        collaborationTime: null,
        sql: null
      },
      database: "MySql",
      queryCollaboration: false,
      queryByTime: true,
      queryByMovieName: false,
      queryByDirectorName: false,
      queryByActorName: false,
      queryByMainActorName: false,
      queryByType: false,
      queryByVersion: false,
      queryByCommenter: false,
      queryByScore: false,
      querySimilarUser: false,
      queryBySql: false,
      showResult: false,
      result: {
        time: 0,
        data: []
      },
      info: null,
      perPage: 20,
      currentPage: 1,
      items: [
        { id: 1, first_name: "Fred", last_name: "Flintstone" },
        { id: 2, first_name: "Wilma", last_name: "Flintstone" },
        { id: 3, first_name: "Barney", last_name: "Rubble" },
        { id: 4, first_name: "Betty", last_name: "Rubble" },
        { id: 5, first_name: "Pebbles", last_name: "Flintstone" },
        { id: 6, first_name: "Bamm Bamm", last_name: "Rubble" },
        { id: 7, first_name: "The Great", last_name: "Gazzoo" },
        { id: 8, first_name: "Rockhead", last_name: "Slate" },
        { id: 9, first_name: "Pearl", last_name: "Slaghoople" }
      ]
    };
  },
  computed: {
    rows() {
      return this.items.length;
    }
  },
  methods: {
    tip(val) {
      return val / 10;
    },
    queryChange(label) {
      this[label] = true;
      this[this.lastQuery] = false;
      this.lastQuery = label;
    },
    submit() {
      var idList = [];
      if (this.queryByTime) {
        idList.push("1");
      } else if (this.queryByMovieName) {
        idList.push("2");
      } else if (this.queryByDirectorName) {
        idList.push("3");
      } else if (this.queryByActorName || this.queryByMainActorName) {
        idList.push("4");
      } else if (this.queryCollaboration) {
        idList.push("5");
      } else if (this.queryByType || this.queryByVersion) {
        idList.push("6");
      } else if (this.queryByCommenter || this.queryByScore) {
        idList.push("7");
      }
      var actors = null;
      var comment = null;
      var directors = null;
      var main_actor = null;
      var movie_name = null;
      var time_from = null;
      var type = null;
      var version = null;
      var answerType = "data";
      var answer = null;
      var time = new Date();
      if (this.queryByTime) {
        time_from = this.formItem.time_from;
        if (this.formItem.time_from.day_of_month == "") {
          time_from.day_of_month = null;
        }
        if (this.formItem.time_from.day_of_week == "") {
          time_from.day_of_week = null;
        }
        if (this.formItem.time_from.month == "") {
          time_from.month = null;
        }
        if (this.formItem.time_from.quarter == "") {
          time_from.quarter = null;
        }
        if (this.formItem.year == "") {
          time_from.year = null;
        }
      } else if (this.queryByMovieName) {
        movie_name = this.formItem.movieName;
      } else if (this.queryByDirectorName) {
        directors = this.formItem.directorName;
      } else if (this.queryByActorName) {
        actors = this.formItem.actorName;
      } else if (this.queryByMainActorName) {
        main_actor = this.formItem.mainActorName;
      } else if (this.queryByType) {
        type = this.formItem.type;
      } else if (this.queryByVersion) {
        version = this.formItem.version;
      } else if (this.queryByCommenter) {
        comment = {};
        comment.id = 1;
        comment.user_name = this.formItem.commenterName;
        comment.score_from = this.formItem.scoreSlider[0] / 10;
        comment.score_to = this.formItem.scoreSlider[1] / 10;
      } else if (this.queryByScore) {
        comment = {};
        comment.id = 2;
        comment.user_name = null;
        comment.score_from = this.formItem.scoreSlider[0] / 10;
        comment.score_to = this.formItem.scoreSlider[1] / 10;
      } else if (this.queryCollaboration) {
      } else if (this.querySimilarUser) {
      }

      if (this.formItem.actorName == null) {
        actors = null;
      }
      var answerType = "data";
      var answer = this.formItem.answer;
      var time = new Date();
      var url = "";
      var params = {};
      if (this.queryCollaboration) {
        params = {
          type: this.formItem.collaborationType,
          threshold: parseInt(this.formItem.collaborationTime)
        };
        if (this.database == "MySql") {
          url = "/MySQL/general/collaboration";
        } else if (this.database == "HBase") {
          url = "/HBase/general/collaboration";
        } else if (this.database == "Neo4j") {
          url = "/Neo4j/general/collaboration";
        }
      } else if (this.querySimilarUser) {
        params = {};
        if (this.database == "MySql") {
          url = "/MySQL/general/similarUser";
        } else if (this.database == "HBase") {
          url = "/HBase/general/similarUser";
        } else if (this.database == "Neo4j") {
          url = "/Neo4j/general/similarUser";
        }
      } else if (this.queryBySql) {
        if (this.database == "MySql") {
          url = "/MySQL/synthesis?sql=" + this.formItem.sql;
        } else if (this.database == "HBase") {
          url = "/HBase/synthesis?sql=" + this.formItem.sql;
        } else if (this.database == "Neo4j") {
          url = "/Neo4j/synthesis?sql=" + this.formItem.sql;
        }
        axios
          .get(url)
          .then(response => {
            console.log(response.data.data);
            var items = [];
            for (var i in response.data.data) {
              console.log(response.data.data[i]);
              items.push({ result: response.data.data[i] });
            }
            this.items = items;
            console.log(this.items);
            this.showResult = true;
            this.result.time = response.data.time;
          })
          .catch(function(error) {
            console.log(error);
          });
          return
      } else {
        params = {
          idList: idList,
          actors: actors,
          comment: comment,
          directors: directors,
          main_actor: main_actor,
          movie_name: movie_name,
          time_from: time_from,
          time_to: null,
          type: type,
          version: version,
          answerType: answerType,
          answer: answer,
          time: time
        };
        if (this.database == "MySql") {
          url = "/MySQL/specify";
        } else if (this.database == "HBase") {
          url = "/HBase/specify";
        } else if (this.database == "Neo4j") {
          url = "/Neo4j/specify";
        }
      }
      console.log(params);
      axios
        .post(url, params)
        .then(response => {
          console.log(response.data.data);
          var items = [];
          for (var i in response.data.data) {
            console.log(response.data.data[i]);
            items.push({ result: response.data.data[i] });
          }
          this.items = items;
          console.log(this.items);
          this.showResult = true;
          this.result.time = response.data.time;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    addActorName() {
      this.formItem.actorName.push(new String());
    },
    addDirectorName() {
      this.formItem.directorName.push(new String());
    },
    addType() {
      this.formItem.type.push(new String());
    },
    addVersion() {
      this.formItem.version.push(new String());
    }
  }
};
</script>
